export default [{
    path: '/articleDetails/:id',
    component: resolve => require(['@/views/articleDetails/articleDetailsPage.vue'], resolve),
    children: [{
        path: '/articleDetails/commentPage/:id',
        component: resolve => require(['@/views/articleDetails/commentPage.vue'], resolve),
    }]
}]


