import Axios from 'axios'
import { actionType } from '../actionType'

const state = {
  article: {
    cover: '',
    club: -1,
    title: '',
    summary: '',
    content: ''
  },
}
const getters = {}
const mutations = {
  [actionType.write.set_article_data] (state, article = {cover, club, title, summary, content}) {
    Object.assign(state.article, article)
  }
}
const actions = {}

export default {
  state,
  getters,
  actions,
  mutations
}



